import Vue from 'vue'
import Router from 'vue-router'

import Home from '../views/Home'
import Leaguer from '../views/Leaguer'
import CureRecord from "../views/cureRecord"
import NotFound from "../views/NotFound"

Vue.use(Router)

const routes = [
    {
        path: "/",
        name: "home",
        component: Home
    },
    {
        path: "/leaguer",
        name: "leaguer",
        component: Leaguer
    },
    {
        path: "/404",
        name: "404",
        component: NotFound
    },
    {
        path: "/cureRecord",
        name: "cureRecord",
        component: CureRecord
    },
    {
        path: "*",
        redirect: { path: '/404' }
    }
]

export default new Router({
    routes
});



