<template>
    <section id="tab_bar">
        <a href="javascript:void(0);" class="home" :class='tab_active === "home" ? "active" : ""' @click='toChang("home")'>功能</a>
        <a href="javascript:void(0);" class="leaguer" :class='tab_active === "leaguer" ? "acvite" : ""' @click='toChang("leaguer")'>会员</a>
    </section>
</template>

<script>
export default {
    data() {
        return {

        }
    },
    methods: {
        toChange(link) {
            if (link === this.$route.name) {
                return;
            }
            if (link === "home") {
                this.$route.push({
                    name: "home"
                });
            } else {
                this.$route.push({
                    name: "leaguer"
                });
            }
        }
    },
    computed: {
        tab_active() {
            return this.$route.name;
        }
    }
}
</script>

<style scoped>

#tab_bar {
    border-top: 1px solid rgba(192, 192, 192, .3);
}
</style>




