// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from "vue";
import App from "./App";
import router from "./router";
import store from "./store";

import {
    getUserInfo
} from "./api";

Vue.config.productionTip = false

let loginStatus = false;
router.beforeEach(async(to, from, next) => {
    if (!loginStatus) {
        let commit = store.commit;
        let oAcquireUserId = document.querySelector("#acquire-userId");
        let userid = JSON.parse(oAcquireUserId.getAttribute("data"));
        let {
            unionid,
            openid
        } = userid;
        if (unionid || unionid !== "") {
            commit("saveUnionid", unionid);
            commit("saveOpenid", openid);

            try {
                let {
                    code,
                    data,
                    message
                } = await getUserInfo({
                    unionid
                });
                console.log(data);

                if (code === "ok") {
                    if (data) {
                        localStorage.setItem("user_info", JSON.stringify({
                            uid: unionid,
                            cid: data.ID,
                            name: data.name,
                            no: data.NO
                        }));

                        commit("saveLoginInfo", {
                            message: "登录成功",
                            status: true,
                            code: 200
                        });
                        commit("saveLeaguerInfo", data);

                        loginStatus = true;
                    }
                } else {
                    console.log("login filed!")
                    commit("saveLoginInfo", {
                        message,
                        status: false,
                        code
                    });
                }

                next();
            } catch (e) {
                console.log(e);
                commit("saveLoginInfo", {
                    message: "登录失败",
                    code: "filed",
                    status: false
                });
            }
        } else next();
    } else next();
})

/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    template: '<App/>',
    components: {
        App
    }
})



