export default {
    saveUnionid(state, uid) {
        state.unionid = uid;
    },
    saveOpenid(state, oid) {
        state.openid = oid;
    },
    saveLeaguerInfo(state, info) {
        state.leaguerInfo = info;
    },
    saveLoginInfo(state, info) {
        state.loginInfo = info;
    }
}



