import axios from "axios";

let base = "/api/v1.1";

export const getUserInfo = params => { return axios.get(`${base}/userInfo`, { params }).then(res => res.data); };

export const cureRecord = params => { return axios.post(`${base}/cureRecord`, params).then(res => res.data); };



